    },
    computed: {
        filteredOptions: function () {

            const filtered = [];
            const regOption = new RegExp(this.input_value, 'ig');
            for (var ii = 0; ii < this.number_options.length; ii++) {
                if (this.input_value.length < 1 || this.number_options[ii].name.match(regOption)) {
                    if (filtered.length < this.maxItem)
                        filtered.push(this.number_options[ii]);
                }
            }
            return filtered;
        },
