                                                                style="font-size: 22px;cursor:pointer;"
                                                                :trackId=data.trackId :trackDesc=data.trackDescription
                                                                :trackBillable=data.isBillable :projectId="data.projectId"
                                                                :taskId=data.taskId></em>
                                                        </div>
                                                        <div v-else>

                                                            <a class="continuetimer"><em :title="$t('ContinueEntry')"
                                                                    class="fa fa-clock-o text-light action-icon"
                                                                    style="color: #e0e8f0 !important; font-size:22px;cursor:pointer;"></em></a>
                                                        </div>
                                                    </div>
                                                    <div v-else>

                                                        <a class="continuetimer"><em :title="$t('ContinueEntry')"
                                                                class="fa fa-clock-o text-dark action-icon"
                                                                style="font-size:22px;cursor:pointer;"></em></a>
                                                    </div>
                                                </div>
                                            </div>

                                            <div class="text-center mobile-action">
                                                <span class="actions mobileaction">
                                                    <em aria-hidden="true"></em>

                                                    <em class="fa fa-ellipsis-h action_icon" aria-hidden="true"
                                                        @click="ToggleActionButton(data.trackId)"></em>
                                                    <span class="action-list-box" style="overflow: visible;"
                                                        v-bind:style="GetIndex === data.trackId ? 'width: 393px; display: block; opacity: 1;' : 'display: none; opacity: 0;'">

                                                        <span class="list-actions p-0" id="action-list">
                                                            <a :class="store.getTimer.trackType != null ? 'tt_disabled' : ''"
                                                                class=" actions-onclick" data-toggle="modal"
                                                                @click="duplicate(data, i)" href="javascript:;"
                                                                v-if="data.trackType != 'Break'">
                                                               
                                                                <span class="text">{{ $t('Duplicate') }} </span>
                                                            </a>
                                                            <a class=" actions-onclick" data-toggle="modal"
                                                                @click="UpdateOrDeleteEntries(data.trackId)"
                                                                href="javascript:;">
                                                                
                                                                <span class="text"> {{ $t('Delete') }}</span>
                                                            </a>
                                                           
                                                        </span> </span>
                                                </span>
                                            </div>
                                        </div>

                                    </div>

                                      </div>



                                </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="w-100 float-right flex-row-reverse mb-2 totalactivity_rt">
                    <div class="float-right tagspanel2">
                     
                     <span class="p-0 w-100">
                        <span>{{ $t('TotalTime') }}: <strong>{{ convertToTimeFormatHHMMSS(item.totalTime) }}</strong></span>
                        <span class="px-0">|</span>

                        <span>{{ $t('UnpaidTime') }}: <strong>{{ convertToTimeFormatHHMMSS(item.unPaidTime) }}</strong></span>
                      
                        
                        <span v-if="store.getTimer.enableBreak != 'yes'" class="px-0">|</span>
                        <span v-if="store.getTimer.enableBreak != 'yes'"> {{ $t('PausedTime') }}: {{
                            convertToTimeFormatHHMMSS(item.pausedHours) }}</span>
                     </span>

                     <span class="coloder">{{ $t('PaidTime') }}: <strong>{{ convertToTimeFormatHHMMSS(item.paidTime) }}</strong></span>

                    </div>
                </div>
            </div>
        </div>

        <loader :isVisible="getLoadingStatus"></loader>
        <DuplicateEntry v-bind:manualData="duplicateEntry" v-if="duplicateShow" v-on:close="duplicateShow = false" />
    </section>
</template>

<style > 
.mobilelistview_base .checkboxx {float: left; width: 50px; text-align: center; padding-left:5px;}

.mobilelistview_base .contentt {float: left; width: calc(100% - 120px);}

.mobilelistview_base .baselinkss {float: left; width: 100%; margin-top: 15px;}

.mobilelistview_base .gt-manual-bar{ border-left: 2px solid #4db567;}

.contentt .dottedbod_rt div{border-right: 1px dashed #c9c9c9; border-left: 0px;}

.contentt .dottedbod_rt div:last-child{border-right:0px;}

.newtimertable .tagspanel2{width:100%; position: relative;}
.totalactivity_rt .tagspanel2 span.coloder {background: #48ccae; color:#fff;}
.totalactivity_rt .tagspanel2 span {float: left; font-size: 14px; padding: 10px 20px; font-weight: 500!important; background:#f6f6f6; color:#777777;}

.newtimertable .baselinkss .actions.mobileaction{top: 15px;}


@media (max-width:1460px){
.tt_status div{max-width: 253px; min-width: 250px;}
}

@media (max-width:1024px){
